/**
 * Gera um Identificador \u00danico Global (GUID) no formato xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx.
 * @returns {string} Uma string representando o GUID.
 */
export function gerarGuid() {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    const r = (Math.random() * 16) | 0,
      v = c === 'x' ? r : (r & 0x3) | 0x8
    return v.toString(16)
  })
}
